.accordion > div.open .header {
  background: #d0d0d0;
}

.accordion > div.open .header::after {
  font: normal normal normal 1em / 1 FontAwesome;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  speak: none;
  text-rendering: auto;
  text-transform: none;
  content: "";
}

.accordion > div.close .header::after {
  font: normal normal normal 1em / 1 FontAwesome;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  speak: none;
  text-rendering: auto;
  text-transform: none;
  content: "";
}

.accordion > div.initially-hidden .content {
  display: none;
}

.accordion > div .header {
  background: #DDD;
  color: #191919;
  cursor: pointer;
  font-size: 1em;
  margin-top: .5em;
  padding: .5em 1em;
  position: relative;
  text-align: left;
  width: 100%;
}

.accordion > div .header:hover {
  background: #d0d0d0;
}

.accordion > div .header::after {
  bottom: 0;
  height: 1em;
  margin: auto 0;
  position: absolute;
  right: 1em;
  top: 0;
  width: 1em;
}

.accordion > div .content {
  padding: 1em 0;
}

