.header-content > .dropdown,
.mobile {
  background-color: #191919;
  bottom: 0;
  display: none;
  height: 100%;
  opacity: 0;
  overflow-y: auto;
  padding: 1rem;
  position: fixed;
  top: 0;
  transition: transform .25s;
  width: 80%;
  z-index: 1000;
}

.header-content > .dropdown.left,
.mobile.left {
  display: block;
  left: 0;
  right: auto;
  transform: translateX(-100%);
}

.header-content > .dropdown.right,
.mobile.right {
  display: block;
  left: auto;
  right: 0;
  transform: translateX(100%);
}

.header-content > .dropdown.left.open, .header-content > .dropdown.right.open,
.mobile.left.open,
.mobile.right.open {
  transform: translateX(0);
  transition: transform .25s;
}

.header-content > .dropdown.init,
.mobile.init {
  display: block;
  opacity: 1;
}

.header-content > .dropdown nav.utility ul li a,
.mobile nav.utility ul li a {
  padding: 0.75rem 0;
}

.header-content > .dropdown ul,
.mobile ul {
  display: block;
  padding-left: 0;
}

.header-content > .dropdown ul li,
.mobile ul li {
  align-items: center;
  border-bottom: 1px solid #333333;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  margin-bottom: 0;
  text-align: left;
}

.header-content > .dropdown ul li a,
.mobile ul li a {
  color: #fff;
  display: block;
  padding: 1rem 0;
}

.header-content > .dropdown ul li i.fa,
.mobile ul li i.fa {
  display: inline-block;
}

@media only screen and (min-width: 1024px) {
  .header-content > .dropdown.open.left,
  .mobile.open.left {
    transform: translateX(-100%);
  }
  .header-content > .dropdown.open.right,
  .mobile.open.right {
    transform: translateX(100%);
  }
}

