    display: none;
  }
  nav.progress a::after, nav.progress a::before {
    border: 0 solid transparent;
    border-bottom: 1.25rem solid transparent;
    border-left: 1rem solid #FFF;
    border-top: 1.25rem solid transparent;
    content: '';
    position: absolute;
    right: 0;
    top: 0;
    z-index: 1;
